import React from "react";
import s from "../Profile.module.css";
import Post from "./Post"


const Posts = (props) => {
    //
    // let postData = [
    //     {id: 1, message: "Hi, how are you", likesCount: "10"},
    //     {id: 2, message: "Its my first post", likesCount: "8"},
    //     {id: 2, message: "Its my second post", likesCount: "7"},
    //     {id: 2, message: "Its my third post", likesCount: "16"},
    //     {id: 3, message: "Yo", likesCount: "14"}
    // ];

    let postsMessages = props.state.map(p => <Post message={p.message} likes={p.likesCount}/>);

    return (


        <div className={s.container}>

            <div className={s.posts}>
                <h3>My posts</h3>
                <form action="">
                    <textarea placeholder="your news"></textarea>
                    <button>Send</button>
                </form>
                {postsMessages}
            </div>
        </div>
    )
};
export default Posts;